import React from "react";
import cn from "classnames";

import { useMobile } from "@hooks/useMobile";

import Button from "@components/Button/Button";
import BlockInfo from "@components/BlockInfo/BlockInfo";
import PreviewText from "@components/PreviewText/PreviewText";

import gallery1 from "@images/Gallery/1.jpg";
import gallery2 from "@images/Gallery/2.jpg";
import gallery3 from "@images/Gallery/3.jpg";
import gallery4 from "@images/Gallery/4.jpg";
import gallery5 from "@images/Gallery/5.jpg";
import gallery6 from "@images/Gallery/6.jpg";
import gallery7 from "@images/Gallery/7.jpg";
import gallery8 from "@images/Gallery/8.jpg";
import gallery9 from "@images/Gallery/9.jpg";
import gallery10 from "@images/Gallery/10.jpg";

const Gallery = () => {
  const [showGallery, setShowGallery] = React.useState(false);
  const isMobile = useMobile();
  const option = {
    sticky: showGallery,
  };

  return (
    <section id="gallery">
      <div className="container">
        <div className="gallery__inside">
          <div className="colum">
            <div className={cn("gallery__inside-left", option)}>
              <PreviewText value="" />
              <BlockInfo
                onClick={() => setShowGallery(!showGallery)}
                nameBtn=" "
                title="   "
                titleRed="   "
                subTitle="  ,        -     "
                btn={!isMobile}
              />
            </div>
          </div>
          <div className="colum">
            <div className="gallery__inside-images">
              <div className="gallery__inside-item">
                <img src={gallery1} alt="" />
              </div>
              <div className="gallery__inside-item">
                <img src={gallery2} alt="" />
              </div>
              {showGallery && (
                <>
                  <div className="gallery__inside-item">
                    <img src={gallery3} alt="" />
                  </div>
                  <div className="gallery__inside-item">
                    <img src={gallery4} alt="" />
                  </div>
                  <div className="gallery__inside-item">
                    <img src={gallery5} alt="" />
                  </div>
                  <div className="gallery__inside-item">
                    <img src={gallery6} alt="" />
                  </div>
                  <div className="gallery__inside-item">
                    <img src={gallery7} alt="" />
                  </div>
                  <div className="gallery__inside-item">
                    <img src={gallery8} alt="" />
                  </div>
                  <div className="gallery__inside-item">
                    <img src={gallery9} alt="" />
                  </div>
                  <div className="gallery__inside-item">
                    <img src={gallery10} alt="" />
                  </div>
                </>
              )}
              {isMobile && (
                <Button
                  onClick={() => setShowGallery(!showGallery)}
                  value=" "
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Gallery;
