import React from "react";
import PreviewText from "@components/PreviewText/PreviewText";
import StatItem from "@components/StatItem/StatItem";
import countImg from "@images/AboutIntro/countimg.jpg";

import ScrollTrigger from "react-scroll-trigger";
import { useMobile } from "@hooks/useMobile";
import { useParallax } from "@hooks/useParallax";

const AboutCounter = () => {
  const isMobile = useMobile();
  const offsetY = useParallax();

  const [startCounter, setStartCounter] = React.useState(false);

  const parallax = {
    counter: { transform: `translateY(-${offsetY / 3}px)` },
    counterMobile: { transform: `translateY(-${offsetY / 70}px)` },
  };

  return (
    <section id="aboutcounter">
      <div
        style={isMobile ? parallax.counterMobile : parallax.counter}
        className="counter"
      >
        <div className="colum">
          <PreviewText value="   " />
          <ScrollTrigger onEnter={() => setStartCounter(true)}>
            <div className="counter-statistics">
              {startCounter && (
                <>
                  <StatItem
                    after=""
                    title={11}
                    subTitle="      "
                  />
                  <StatItem
                    before=">"
                    title={600000}
                    subTitle="         "
                  />
                  <StatItem
                    after="3"
                    title={19800}
                    subTitle="   "
                  />
                  <StatItem
                    after=""
                    title={45}
                    subTitle=" -  "
                  />
                  <StatItem
                    before=">"
                    title={180000}
                    subTitle="    "
                  />
                  <StatItem
                    before=">"
                    title={400000}
                    subTitle="    "
                  />
                </>
              )}
            </div>
          </ScrollTrigger>
        </div>
        <div className="colum">
          <div className="aboutCounter-img">
            <img src={countImg} alt=" " />
          </div>
        </div>
      </div>
    </section>
  );
};

export default AboutCounter;
